<template>
  <div class="main">
    <div class="top-nav">
      <div class="logos">
        <img src="../assets/img/logo-zc.png" alt />
        <img src="../assets/img/logo-ys.png" alt />
      </div>
      <div class="top-btn-group">
        <div :class="{'select-icon':selectedRole === 'supply'}">
          <span>
            <i class="iconfont icon-gongyingshang"></i>
          </span>
          <span>{{$t('m').supply}}</span>
          <span style="color:red">{{$t('m').aBalance}}:{{supplyBalance}}{{$t('m').yuan}}</span>
        </div>
        <div :class="{'select-icon':selectedRole === 'buyer'}">
          <span>
            <i class="iconfont icon-maijia"></i>
          </span>
          <span>{{$t('m').buyer}}</span>
          <span style="color:red">{{$t('m').aBalance}}:{{buyerBalance}}{{$t('m').yuan}}</span>
        </div>
        <div :class="{'select-icon':selectedRole === 'logistics'}">
          <span>
            <i class="iconfont icon-wuliu"></i>
          </span>
          <span>{{$t('m').logistics}}</span>
          <span style="color:red">{{$t('m').aBalance}}:{{logisticsBalance}}{{$t('m').yuan}}</span>
        </div>
        <!-- <div @click="changeLanguage" style="width:120px;cursor:pointer">
          <span>{{$t('m').changeLanguage}}</span>
        </div>-->
      </div>
    </div>
    <div class="left-menu">
      <router-link
        tag="div"
        :class="{'selected-item':$route.name === 'Goods'}"
        to="goods"
      >{{$t('m').commodityList}}</router-link>
      <router-link
        tag="div"
        :class="{'selected-item':$route.name === 'OrderList'}"
        to="orderlist"
      >{{$t('m').orderList}}</router-link>
      <router-link
        tag="div"
        :class="{'selected-item':$route.name === 'AddGoods'}"
        to="addgoods"
        v-if="selectedRole === 'supply'"
      >{{$t('m').addGoods}}</router-link>
      <router-link
        tag="div"
        :class="{'selected-item':$route.name === 'Stock'}"
        to="stock"
        v-if="selectedRole === 'supply'"
      >{{$t('m').inventory}}</router-link>
      <router-link tag="span" class="logout-btn" to="/select">{{$t('m').logout}}</router-link>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Main',
  data() {
    return {
      selectedRole: this.$route.params.role,
      accounts: {
        supply: 1,
        buyer: 3,
        logistics: 2
      },
      balances: [0, 0, 0, 0]
    }
  },
  created() {
    for (let k in this.accounts) {
      this.axios.post(this.$store.state.urls.roleAccount, {
        account_id: this.accounts[k] + ''
      }).then(response => {
        this.$set(this.balances, this.accounts[k], response.data[0].balance)
      })
    }
  },
  computed: {
    supplyBalance() {
      return this.balances[1]
    },
    buyerBalance() {
      return this.balances[3]
    },
    logisticsBalance() {
      return this.balances[2]
    }
  },
  methods: {
    changeLanguage() {
      if (this.lang === 'zh-CN') {
        this.lang = 'en-US'
        this.$i18n.locale = this.lang// 关键语句
      } else {
        this.lang = 'zh-CN'
        this.$i18n.locale = this.lang// 关键语句
      }
    }
  }
}
</script>
<style scoped>
@import url("../assets/icon/iconfont.css");
html,
body {
  overflow: hidden;
}
.main {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.top-nav {
  height: 92px;
  width: 100%;
  padding-right: 100px;
  box-sizing: border-box;
  padding-left: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 1158px;
}
.logos {
  height: 100%;
  line-height: 92px;
}
.logos img {
  display: inline-block;
  vertical-align: middle;
}
.logos img:first-child {
  height: 47px;
  width: 47px;
}
.logos img:last-child {
  height: 40px;
  width: 170px;
}
.top-btn-group {
  height: 100%;
  display: flex;
  font-size: 13px;
}
.top-btn-group div {
  margin-right: 45px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.top-btn-group i {
  font-size: 23px;
  color: #1e1e1e;
}
.top-btn-group span:last-child {
  margin-top: 5px;
}
.left-menu {
  width: 158px;
  height: calc(100% - 92px);
  background-color: #ffffff;
  box-shadow: 0px 1px 4px 0px rgba(35, 35, 35, 0.11);
  position: fixed;
  left: 0;
  top: 92px;
  text-align: center;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.left-menu div {
  height: 60px;
  width: 120px;
  line-height: 60px;
  border-bottom: 1px solid #f3f2f2;
  cursor: pointer;
}
.content {
  width: calc(100% - 158px);
  margin-left: 158px;
  height: calc(100% - 92px);
  overflow: auto;
}
.logout-btn {
  color: white;
  font-size: 16px;
  line-height: 36px;
  position: absolute;
  bottom: 10px;
  width: 113px;
  height: 36px;
  -webkit-border-radius: 18px;
  -moz-border-radius: 18px;
  border-radius: 18px;
  background-color: #9ac258;
  background-image: -webkit-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -moz-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -o-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -ms-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: linear-gradient(to right, #44b3f9, #64e1ff);
  cursor: pointer;
}
.select-icon {
  color: rgb(72, 185, 250) !important;
}
.select-icon i {
  color: rgb(72, 185, 250) !important;
}
.selected-item {
  border-bottom-color: #258ccd !important;
}
</style>



