<template>
  <div class="container" v-loading="loading">
    <order-check :order-num="selectedOrder" v-if="openCheck" @close="openCheck=false"></order-check>
    <p class="top-input">
      <span class="top-input-span">
        <input type="text" v-model="searchText" />
        <span class="search-icon">
          <i class="iconfont icon-sousuo"></i>
        </span>
      </span>
    </p>
    <div class="order-list-container">
      <vue-scroll>
        <div class="order-list">
          <div class="order-item" v-for="(order,index) in showOrders" :key="index">
            <div class="item-icon">
              <span>
                <i class="iconfont icon-shangpintubiao"></i>
              </span>
            </div>
            <div class="item-content">
              <p>
                <span>{{$t('m').createTime}}:</span>
                <span>{{ formatDate(order.orderTime) }}</span>
              </p>
              <p>
                <span>{{$t('m').orderNum}}:</span>
                <span>{{ order.id }}</span>
              </p>
              <p>
                <span>{{$t('m').commodityName}}:</span>
                <span>{{ order.commodity.name }}</span>
              </p>
              <p>
                <span>{{$t('m').price}}:</span>
                <span>{{ order.commodity.price }}</span>
              </p>
              <p>
                <span>{{$t('m').count}}:</span>
                <span>{{ order.quantity }}</span>
              </p>
              <p>
                <span>{{$t('m').blockchainId}}:</span>
                <span>{{order.transaction_id}}</span>
              </p>
            </div>
            <div class="item-status">
              <span class="item-status-span" :class="{'item-status-text':order.status !== '完成'}">
                <i
                  class="iconfont icon-gou"
                  v-if="order.status === '完成'"
                  @click="openCheck=true;selectedOrder=order.id"
                ></i>
                <span
                  v-if="order.status !== '完成'"
                  @click="$router.push(`transport?id=${order.id}`)"
                >{{ order.status=== '新建'?'详情':order.status }}</span>
              </span>
              <span
                class="item-check-btn"
                @click="checkOrder(order.id)"
                v-if="selectedRole==='buyer'&&order.status !== '完成'"
              >{{$t('m').confirmation}}</span>
              <span
                class="item-check-btn"
                @click="checkSend(order.id)"
                v-if="selectedRole==='logistics'&&order.status === '新建'"
              >{{$t('m').confirmation}}</span>
            </div>
          </div>
        </div>
      </vue-scroll>
    </div>
  </div>
</template>
<script>
import moment from 'moment'
import OrderCheck from './OrderCheck'
export default {
  name: 'OrderList',
  components: {
    'order-check': OrderCheck
  },
  data() {
    return {
      orders: [],
      loading: false,
      searchText: '',
      selectedRole: this.$route.params.role,
      selectedOrder: '',
      openCheck: false
    }
  },
  created() {
    this.init()
  },
  computed: {
    showOrders: function() {
      return this.orders.filter(order => {
        let str = ''
        for (let key in order) {
          str += (key + order[key] + ',')
        }
        return !this.searchText || str.indexOf(this.searchText) > -1
      })
    }
  },
  methods: {
    init() {
      this.loading = true
      this.axios.get(this.$store.state.urls.getOrderList)
        .then(response => {
          this.loading = false
          this.orders = response.data
        })
        .catch(error => {
          this.loading = false
          console.log(error)
        })
    },
    formatDate(time) {
      return moment(new Date(time).getTime()).format('YYYY-MM-DD HH:mm:ss')
    },
    checkOrder(id) {
      this.loading = true
      this.axios.post(this.$store.state.urls.updateOrderStatus, {
        order_id: id,
        status: 'Done'
      })
        .then(response => {
          this.loading = false
          this.init()
        })
        .catch(error => {
          this.loading = false
          console.log(error)
        })
    },
    checkSend(id) {
      this.loading = true
      this.axios.post(this.$store.state.urls.updateOrderStatus, {
        order_id: id,
        status: 'Processing'
      })
        .then(response => {
          this.loading = false
          this.init()
        })
        .catch(error => {
          this.loading = false
          console.log(error)
        })
    }
  }
}
</script>
<style scoped>
@import url("../assets/icon/iconfont.css");
.container {
  height: 100%;
  width: 100%;
}
.order-list-container {
  height: 700px;
  width: 100%;
}
.order-list {
  height: 100%;
  width: 100%;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.order-item {
  width: calc(100% - 80px);
  max-width: 1000px;
  height: 173px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  background-color: #44b3f9;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.5);
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.5);
  background-image: -webkit-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -moz-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -o-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -ms-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: linear-gradient(to right, #44b3f9, #64e1ff);
  display: flex;
  margin-bottom: 15px;
  cursor: pointer;
}
.item-icon {
  height: 100%;
  width: 173px;
  display: flex;
  justify-content: center;
  align-items: center;
  float: left;
}
.item-icon i {
  font-size: 80px;
  color: white;
}
.item-content {
  flex-grow: 1;
  padding-top: 10px;
  text-align: left;
  color: white;
  font-size: 14px;
  letter-spacing: 2px;
}
.item-content > p:last-child {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.item-status {
  width: 180px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.item-status-span {
  height: 98px;
  width: 98px;
  -webkit-border-radius: 49px;
  -moz-border-radius: 49px;
  border-radius: 49px;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.23);
  -moz-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.23);
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.23);
  line-height: 98px;
  cursor: pointer;
}
.item-status-span span {
  font-size: 20px;
}
.item-status i {
  color: white;
  font-size: 98px;
}
.item-content p {
  margin: 0;
  margin-bottom: 2px;
  height: 19px;
}
.top-input {
  height: 40px;
  margin: 0;
  margin-bottom: 30px;
  text-align: center;
}
.top-input-span {
  position: relative;
}
.top-input input {
  border: none;
  outline: none;
  width: calc(100% - 80px);
  max-width: 1000px;
  height: 40px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  -moz-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  padding: 0 50px 0 20px;
}
.search-icon {
  position: absolute;
  right: 15px;
  color: rgb(116, 116, 116);
  line-height: 40px;
  cursor: pointer;
}
.search-icon i {
  font-size: 23px;
}
.item-status-text {
  background-color: white;
}
.item-check-btn {
  background-color: white;
  width: 80px;
  height: 30px;
  margin-top: 20px;
  line-height: 30px;
  border-radius: 5px;
  font-size: 14px;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  -moz-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  cursor: pointer;
}
</style>



