import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const urlPre = ''

export default new Vuex.Store({
  state: {
    loginMsg: {
      name: '盈盛',
      password: '12345678'
    },
    urls: {
      getAllGood: urlPre + '/commodityList',
      createOrder: urlPre + '/createOrder',
      getOrderList: urlPre + '/orderList',
      getOrderByOrderNum: urlPre + '/orderList?orderId=',
      updateOrderStatus: urlPre + '/updateOrderStatus',
      createGood: urlPre + '/createCommodity',
      ordersDetail: urlPre + '/orderList',
      roleAccount: urlPre + '/accountList '
    }
  },
  mutations: {
  }
})



