<template>
  <div class="select">
    <div class="logos">
      <img src="../assets/img/logo-zc-white.png" alt />
      <img src="../assets/img/logo-ys-white.png" alt />
    </div>
    <div class="select-content-container">
      <div class="select-content">
        <router-link tag="div" class="select-item" to="/web/main/supply/orderlist">
          <div class="select-item-logo">
            <span>
              <i class="iconfont icon-gongyingshang"></i>
            </span>
          </div>
          <div class="select-item-content">
            <p>{{$t('m').supply}}</p>
            <div>&nbsp;&nbsp;&nbsp;&nbsp;{{$t('m').supplyText}}</div>
          </div>
        </router-link>
        <router-link tag="div" class="select-item" to="/web/main/buyer/orderlist">
          <div class="select-item-logo">
            <span>
              <i class="iconfont icon-maijia"></i>
            </span>
          </div>
          <div class="select-item-content">
            <p>{{$t('m').buyer}}</p>
            <div>&nbsp;&nbsp;&nbsp;&nbsp;{{$t('m').buyerText}}</div>
          </div>
        </router-link>
        <router-link tag="div" class="select-item" to="/web/main/logistics/orderlist">
          <div class="select-item-logo">
            <span>
              <i class="iconfont icon-wuliu"></i>
            </span>
          </div>
          <div class="select-item-content">
            <p>{{$t('m').logistics}}</p>
            <div>&nbsp;&nbsp;&nbsp;&nbsp;{{$t('m').logisticsText}}</div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Select',
  data() {
    return {

    }
  }
}
</script>
<style scoped>
@import url("../assets/icon/iconfont.css");
.select {
  height: 100%;
  width: 100%;
  background-image: url("../assets/img/select-bg.png");
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.select-item {
  width: 276px;
  height: 354px;
  cursor: pointer;
  background-color: white;
}
.select-item:nth-child(1) {
  border-radius: 10px 0 0 10px;
}
.select-item:nth-child(3) {
  border-radius: 0 10px 10px 0;
}
.select-item:hover {
  width: 291px;
  height: 344px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  background-color: #fff;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.35);
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.35);
  z-index: 9999;
}
.select-content-container {
  width: 900px;
  height: 400px;
  vertical-align: middle;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.select-content {
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  display: inline-flex;
  align-items: center;
}
.select-item-logo {
  height: 45%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.select-item-logo span {
  background-image: url("../assets/img/select-item-bg.png");
  height: 117px;
  width: 117px;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}
.select-item-logo i {
  color: white;
  font-size: 46px;
}
.select-item-content {
  height: 53%;
  padding: 0 30px 0 30px;
  box-sizing: border-box;
  text-align: center;
}
.select-item-content div {
  font-size: 13px;
  text-align: left;
  line-height: 14px;
  width: 220px;
  display: inline-block;
}
.logos {
  top: 20px;
  left: 40px;
  position: absolute;
}
.logos img:first-child {
  height: 60px;
  width: 170px;
}
.logos img:last-child {
  height: 50px;
  width: 170px;
}
</style>



