<template>
  <div class="add-goods" v-loading="loading">
    <div class="content">
      <p class="content-title">
        <span>{{$t('m').addGoods}}</span>
        <span class="add-btn" @click="addGood">{{$t('m').add}}</span>
      </p>
      <div class="content-form-group">
        <div>
          <p>
            <span>{{$t('m').commodityName}}</span>
          </p>
          <input type="text" v-validate="'required'" v-model="commodity.name" name="name" />
        </div>
        <div>
          <p>
            <span>{{$t('m').sourceArea}}</span>
          </p>
          <input type="text" v-validate="'required'" v-model="commodity.origin" name="origin" />
        </div>
        <div>
          <p>
            <span>{{$t('m').maxTemp}}(℃)</span>
          </p>
          <input
            type="number"
            v-validate="'required'"
            v-model="commodity.highTemperature"
            name="highTemperature"
          />
        </div>
        <div>
          <p>
            <span>{{$t('m').minTemp}}(℃)</span>
          </p>
          <input
            type="number"
            v-validate="'required'"
            v-model="commodity.lowTemperature"
            name="lowTemperature"
          />
        </div>
        <div>
          <p>
            <span>{{$t('m').price}}</span>
          </p>
          <input type="number" v-validate="'required'" v-model="commodity.price" name="price" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'AddGoods',
  data() {
    return {
      commodity: {
        name: '',
        id: new Date().getTime() + '',
        origin: '',
        lowTemperature: 0,
        highTemperature: 0,
        price: 0,
        owner: '1'
      },
      loading: false
    }
  },
  methods: {
    addGood() {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.commodity.price = parseFloat(this.commodity.price)
          this.axios.post(this.$store.state.urls.createGood, this.commodity)
            .then(response => {
              this.$message({
                message: this.$t('m').addGoodSuccess,
                type: 'success'
              })
              this.commodity = {
                name: '',
                id: new Date().getTime() + '',
                origin: '',
                lowTemperature: 0,
                highTemperature: 0,
                price: 0,
                owner: '1'
              }
            })
            .catch(error => {
              console.log(error)
            })
        } else {
          this.$message({
            message: this.$t('m').submitTip,
            type: 'error'
          })
        }
      })
    }
  }
}
</script>
<style scoped>
.add-goods {
  height: 100%;
  width: 100%;
  text-align: center;
  overflow-y: auto;
  overflow-x: hidden;
}
.content {
  display: inline-block;
  width: calc(100% - 80px);
  max-width: 1000px;
}
.content-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  margin: 0;
  margin-bottom: 50px;
}
.content-title span:first-child {
  font-size: 30px;
  font-weight: bold;
}
.add-btn {
  width: 113px;
  height: 36px;
  -webkit-border-radius: 18px;
  -moz-border-radius: 18px;
  border-radius: 18px;
  background-color: #9ac258;
  background-image: -webkit-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -moz-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -o-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -ms-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: linear-gradient(to right, #44b3f9, #64e1ff);
  line-height: 36px;
  color: white;
  cursor: pointer;
}
.content-form-group {
  text-align: left;
  margin-bottom: 20px;
}
.content-form-group p {
  font-size: 23px;
}
.content-form-group input {
  height: 40px;
  font-size: 20px;
  border: none;
  border-bottom: 1px solid #f3f2f2;
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  box-sizing: border-box;
  outline: none;
}
.content-form-group input:focus {
  border-bottom: 2px solid #258ccd;
}
</style>



