<template>
  <div class="goods">
    <div class="buy-window-bg" v-if="buy" v-loading="loading">
      <div class="buy-window">
        <p class="buy-window-title">
          {{$t('m').commodity}}：
          <span>{{selectGood.name}}</span>
        </p>
        <p>
          <span>{{$t('m').count}}</span>
          <el-input-number v-model="buyMsg.quantity" :min="1"></el-input-number>
        </p>
        <p>
          <span>{{$t('m').name}}</span>
          <input type="text" v-validate="'required'" name="name" />
        </p>
        <p>
          <span>{{$t('m').phone}}</span>
          <input type="text" v-validate="'required'" name="phone" />
        </p>
        <p>
          <span>{{$t('m').address}}</span>
          <input type="text" v-model="buyMsg.deliverAddress" v-validate="'required'" name="address" />
        </p>
        <span
          class="check-btn"
          @click="addOrderPost"
          v-if="selectedRole === 'buyer'"
        >{{$t('m').buy}}</span>
        <span class="close-btn" @click="buy=false">
          <i class="iconfont icon-X"></i>
        </span>
      </div>
    </div>
    <div class="goods-content-container">
      <div class="goods-content">
        <div class="goods-item" v-for="(good,index) in goods" :key="index">
          <img src="../assets/img/apple.png" alt />
          <div class="good-msg">
            <p>{{ good.name }}</p>
            <p>
              <span>¥&nbsp;&nbsp;</span>
              <span>{{ good.price }}{{$t('m').yuan}}</span>
            </p>
            <p>
              {{$t('m').placeOfDispatch}}：
              <span>{{ good.origin }}</span>
            </p>
          </div>
          <span class="buy-btn" v-if="selectedRole === 'buyer'">
            <i class="iconfont icon-gouwuche" @click="buy=true;selectGood=good"></i>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Goods',
  data() {
    return {
      loading: false,
      buy: false,
      goods: [],
      selectGood: {},
      buyMsg: {
        commodity_id: '',
        id: '',
        deliverAddress: '',
        deliverTime: '',
        quantity: 1,
        buyer: '3',
        seller: '1'
      },
      selectedRole: this.$route.params.role

    }
  },
  created() {
    this.axios
      .get(this.$store.state.urls.getAllGood)
      .then(response => {
        this.goods = response.data
      })
      .catch(error => {
        console.log(error)
      })
  },
  methods: {
    addOrderPost() {
      this.$validator.validateAll().then(result => {
        if (result && this.buyMsg.quantity > 0) {
          this.loading = true
          this.axios
            .post(this.$store.state.urls.createOrder, this.buyMsg)
            .then(response => {
              this.loading = false
              this.buy = false
              this.$message({
                message: this.$t('m').orderSuccess,
                type: 'success'
              })
            })
            .catch(error => {
              this.loading = false

              console.log(error)
              this.$message({
                message: this.$t('m').orderError,
                type: 'error'
              })
            })
        } else if (this.buyMsg.quantity <= 0) {
          this.$message({
            message: this.$t('m').legalQuantity,
            type: 'warning'
          })
        } else {
          this.$message({
            message: this.$t('m').fillInfo,
            type: 'warning'
          })
        }
      })
    }
  },
  watch: {
    selectGood(curVal, oldVal) {
      if (curVal) {
        this.buyMsg.id =
          new Date().getTime() + Math.floor(Math.random() * 1000) + ''
        this.buyMsg.commodity_id = curVal.id
        this.buyMsg.deliverTime =
          new Date().getTime() + 24 * 60 * 60 * 1000 * 3
      }
    }
  }
}
</script>
<style scoped>
@import url("../assets/icon/iconfont.css");
.goods {
  height: 100%;
  width: 100%;
  overflow-y: auto;
  text-align: center;
}
.goods-item {
  width: 200px;
  height: 241px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  background-color: #fff;
  -webkit-box-shadow: 0 1px 4px rgba(35, 35, 35, 0.11);
  -moz-box-shadow: 0 1px 4px rgba(35, 35, 35, 0.11);
  box-shadow: 0 1px 4px rgba(35, 35, 35, 0.11);
  margin-right: 30px;
  overflow: hidden;
  position: relative;
  margin-bottom: 30px;
}
.goods-item img {
  height: 60%;
  width: 100%;
}
.goods-content-container {
  display: inline-block;
  height: 700px;
  padding-top: 50px;
  width: calc(100% - 80px);
  max-width: 1000px;
}
.goods-content {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.good-msg {
  height: 40%;
  padding-left: 10px;
}
.good-msg p {
  margin: 0;
}
.good-msg p:nth-child(1) {
  font-size: 20px;
  text-align: left;
  margin-bottom: 5px;
}
.good-msg p:nth-child(2) {
  font-size: 14px;
  text-align: left;
  color: #d21e34;
  margin-bottom: 5px;
}
.good-msg p:nth-child(3) {
  font-size: 12px;
  text-align: left;
  color: #b6b9be;
}
.buy-btn {
  position: absolute;
  width: 40px;
  height: 30px;
  background-image: -webkit-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -moz-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -o-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: -ms-linear-gradient(left, #44b3f9, #64e1ff);
  background-image: linear-gradient(to right, #44b3f9, #64e1ff);
  right: 0;
  bottom: 0;
  line-height: 30px;
  cursor: pointer;
}
.buy-btn i {
  color: white;
  font-size: 21px;
}
.buy-window-bg {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1999;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.2);
}
.buy-window-bg::after {
  content: "";
  height: 100%;
  width: 0;
  display: inline-block;
  vertical-align: middle;
}
.buy-window {
  width: 335px;
  height: 321px;
  background-color: #fff;
  -webkit-box-shadow: 0 0 6px rgba(224, 224, 224, 0.4);
  -moz-box-shadow: 0 0 6px rgba(224, 224, 224, 0.4);
  box-shadow: 0 0 6px rgba(224, 224, 224, 0.4);
  display: inline-block;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  text-align: left;
  padding: 10px 40px 10px 40px;
  box-sizing: border-box;
  position: relative;
}
.buy-window-title {
  font-size: 20px;
  margin-bottom: 10px;
}
.buy-window p:not(:first-child) {
  color: #b6b9be;
  font-size: 18px;
  height: 30px;
  line-height: 30px;
  margin: 0;
  margin-top: 10px;
}
.buy-window p:not(:first-child) span {
  margin-right: 15px;
  vertical-align: bottom;
}
.buy-window p:nth-child(2) {
  margin-top: 0px;
  margin-bottom: 20px;
}
.buy-window p:nth-child(2) input {
  width: 70px;
}
.buy-window p:not(:nth-child(2)) input {
  border: none;
  border-bottom: 1px solid #999999;
  outline: none;
  height: 30px;
}
.check-btn {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  margin: auto;
  width: 153px;
  height: 40px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  background-color: #44b3f9;
  color: white;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}
.close-btn {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}
</style>



