import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import OrderList from '@/components/OrderList'
import AddGoods from '@/components/AddGoods'
import Stock from '@/components/Stock'
import Goods from '@/components/Goods'
import Select from '@/components/Select'
// 运输信息界面
import Transport from '@/components/Transport'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/web/main/:role/',
      name: 'Main',
      component: Main,
      redirect: '/web/main/:role/orderList',
      children: [
        {
          path: 'orderlist',
          name: 'OrderList',
          component: OrderList
        },
        {
          path: 'addgoods',
          name: 'AddGoods',
          component: AddGoods
        },
        {
          path: 'stock',
          name: 'Stock',
          component: Stock
        },
        {
          path: 'goods',
          name: 'Goods',
          component: Goods
        },
        {
          path: 'transport',
          name: 'Transport',
          component: Transport,
          props: (route) => ({ id: route.query.id })
        }
      ]
    },
    {
      path: '/web/select',
      name: 'Select',
      component: Select
    },
    {
      path: '*',
      redirect: '/web/select'
    }

  ]
})



