// 传输图标（飞机）
const PLANE_PATH =
  'path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z'

export default {
  data() {
    return {
      mapOptions: {
        backgroundColor: '#404a59',
        title: {
          text: this.$t('m').haulway,
          left: 'center',
          textStyle: {
            color: '#fff'
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: function(params, ticket, callback) {
            if (params.seriesType === 'effectScatter') {
              return params.data.name
            } else if (params.seriesType === 'lines') {
              return params.data.fromName + ' --> ' + params.data.toName
            } else {
              return params.name
            }
          }
        },
        geo: {
          map: 'china',
          label: {
            emphasis: {
              show: true,
              color: '#fff'
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#323c48',
              borderColor: '#404a59',
              borderWidth: 1
            },
            emphasis: {
              areaColor: '#2a333d'
            }
          }
        },
        series: [
          {
            name: 'path',
            type: 'lines',
            zlevel: 1,
            effect: {
              show: true,
              period: 6,
              trailLength: 0.7,
              // arrow箭头的颜色
              color: 'red',
              symbolSize: 3
            },
            lineStyle: {
              normal: {
                color: '#46bee9',
                width: 0,
                curveness: 0.2
              }
            },
            data: []
          },
          {
            name: 'path',
            type: 'lines',
            zlevel: 2,
            symbol: ['none', 'arrow'],
            symbolSize: 10,
            effect: {
              show: true,
              period: 6,
              trailLength: 0,
              symbol: PLANE_PATH,
              symbolSize: 15
            },
            lineStyle: {
              normal: {
                color: '#46bee9',
                width: 1,
                opacity: 0.6,
                curveness: 0.2
              }
            },
            data: []
          },
          {
            name: 'path',
            type: 'effectScatter',
            coordinateSystem: 'geo',
            zlevel: 2,
            rippleEffect: {
              brushType: 'stroke'
            },
            label: {
              normal: {
                show: true,
                position: 'right',
                formatter: '{b}'
              }
            },
            symbolSize: function(val) {
              return val[2] / 8
            },
            itemStyle: {
              normal: {
                color: '#46bee9'
              },
              emphasis: {
                areaColor: '#2B91B7'
              }
            },
            data: []
          }
        ]
      },
      tempOptions: {
        tooltip: {
          trigger: 'axis',
          position: function(pt) {
            return [pt[0], '10%']
          }
        },
        title: {
          left: 'center',
          text: '实时温度'
        },
        grid: {
          left: '3%',
          right: '5%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%'],
          splitLine: {
            show: false
          }
        },
        dataZoom: [
          {
            type: 'inside',
            start: 0,
            end: 100
          },
          {
            start: 95,
            end: 100,
            handleIcon:
              'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
            handleSize: '80%',
            handleStyle: {
              color: '#fff',
              shadowBlur: 3,
              shadowColor: 'rgba(0, 0, 0, 0.6)',
              shadowOffsetX: 2,
              shadowOffsetY: 2
            },
            left: 160,
            right: 160
          }
        ],
        series: [
          {
            name: 'temp',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              color: '#fff'
            },
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
                {
                  offset: 0,
                  color: '#44b3f9'
                },
                {
                  offset: 1,
                  color: '#64e1ff'
                }
              ])
            },
            data: []
          }
        ]
      },
      requestDetailObj: {
        timeStamp: null
      },
      // 更新图表的时间间隔
      updateAllChartsTimeSpan: 6000,
      // 更新图表的定时器
      updateAllChartsInterval: null
    }
  },
  props: {
    id: String
  },
  created() {
    this.judgeId()
    // 注册地图
    echarts.registerMap('china', CHINA_MAP)
    this.updateAllCharts()
    // 定时刷新图表
    this.updateAllChartsInterval = setInterval(() => {
      this.updateAllCharts()
    }, this.updateAllChartsTimeSpan)
  },
  methods: {
    /**
     * 判断订单id是否为空，为空则跳转到Main页面
     */
    judgeId() {
      if (!this.id) {
        this.$router.push('../')
      }
    },
    /**
     * 转化线路数据
     */
    updateMap(data) {
      let convertedData = []
      for (let i = 0; i < data.length; i++) {
        let dataItem = data[i]
        let fromName = dataItem.from
        let toName = dataItem.to
        let fromCoord = GEO_COORD_MAP[fromName]
        let toCoord = GEO_COORD_MAP[toName]
        if (fromCoord && toCoord) {
          convertedData.push({
            fromName,
            toName,
            coords: [fromCoord, toCoord],
            value: 10
          })
        }
      }

      let { series } = this.mapOptions
      series[0].data = convertedData
      series[1].data = convertedData
      let showPointsObj = {}
      let showPoints = []
      data.forEach(item => {
        showPointsObj[item.from] = true
        showPointsObj[item.to] = true
      })
      for (let prop in showPointsObj) {
        showPoints.push({
          name: prop,
          value: GEO_COORD_MAP[prop].concat([100])
        })
      }
      series[2].data = showPoints
    },
    /**
     * 更新所有图表
     */
    updateAllCharts() {
      let timeStamp = new Date().getTime()
      let { requestDetailObj } = this
      requestDetailObj.timeStamp = timeStamp
      this.getOrderDetail().then((detail) => {
        if (timeStamp !== requestDetailObj.timeStamp) {
          return
        }

        // 更新线路
        let pathData = [
          {
            from: '广州',
            to: '上海'
          },
          {
            from: '上海',
            to: '北京'
          }
        ]
        this.updateMap(pathData)

        // 更新温度表
        // debugger
        let temperatureDatas = detail.temperatureVariation
        let date = []
        let data = []
        for (let temItem of temperatureDatas) {
          date.push(
            moment(
              moment(temItem.record_time).valueOf() + this.updateAllChartsTimeSpan
            ).format('YY-MM-DD HH:mm:ss')
          )
          data.push(temItem.temperature)
        }
        this.updateTemp(date, data)
      // let date = this.tempOptions.xAxis.data
      // let data = this.tempOptions.series[0].data
      // date.push(
      //   moment(
      //     moment(date[date.length - 1], 'YY-MM-DD HH:mm:ss').valueOf() + this.updateAllChartsTimeSpan
      //   ).format('YY-MM-DD HH:mm:ss')
      // )
      // data.push(this.randomTemp(data[data.length - 1]))
      // date.shift()
      // data.shift()
      // this.updateTemp(date, data)
      }).catch((err) => {
        if (timeStamp !== requestDetailObj.timeStamp) {
          return
        }
        alert(err.message)
      })
    },
    randomTemp(lastTemp = 20) {
      let random = Math.random() - 0.5 + lastTemp
      return random < 10 ? 10 : random > 30 ? 30 : random
    },
    updateTemp(date, data) {
      let { tempOptions } = this
      tempOptions.xAxis.data = date
      tempOptions.series[0].data = data
    },
    /**
     * 获取订单详情
     */
    getOrderDetail() {
      return new Promise((resolve, reject) => {
        this.axios.get(`${this.$store.state.urls.ordersDetail}?orderId=${this.id}`).then((res) => {
          let {data} = res
          if (!data || !data.length) {
            return reject(new Error('The data is empty'))
          }
          resolve(data[0])
        }).then((reason) => {
          reject(reason)
        })
      })
    }
  },
  watch: {
    id() {
      this.judgeId()
    }
  },
  destroyed() {
    clearInterval(this.updateAllChartsInterval)
  }
}
</script>

<style scoped>
.charts-content {
  width: 60%;
  min-width: 800px;
  margin: 0 auto;
}
.chart-wrapper {
  margin-bottom: 20px;
  width: 100%;
}
.chart-wrapper:last-child {
  margin-bottom: 0;
}
.echarts {
  width: 100%;
  height: 100%;
}
.path-map {
  height: 420px;
}
.temperature {
  height: 300px;
}
</style>



