<template>
  <div class="stock">
    <p class="top-input">
      <span class="top-input-span">
        <input type="text" />
        <span class="search-icon">
          <i class="iconfont icon-sousuo"></i>
        </span>
      </span>
    </p>
    <div class="stock-table">
      <el-table
        :data="tableData"
        stripe
        style="width: 100%"
        height="700"
        row-class-name="stock-table-row"
        header-row-class-name="stock-table-header"
      >
        <el-table-column prop="name" :label="$t('m').commodityName"></el-table-column>
        <el-table-column prop="status" :label="$t('m').inventoryStatus"></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Stock',
  data() {
    return {
      tableData: []
    }
  },
  created() {
    this.axios
      .get(this.$store.state.urls.getAllGood)
      .then(response => {
        this.goods = response.data
        this.goods.forEach(element => {
          this.tableData.push({name: element.name, status: '有货'})
        })
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>
<style scoped>
.stock {
  height: 100%;
  width: 100%;
  overflow-y: auto;
  text-align: center;
}
.stock-table {
  width: calc(100% - 80px);
  max-width: 1000px;
  display: inline-block;
}
.top-input {
  height: 40px;
  margin: 0;
  margin-bottom: 30px;
  text-align: center;
}
.top-input-span {
  position: relative;
}
.top-input input {
  border: none;
  outline: none;
  width: calc(100% - 80px);
  max-width: 1000px;
  height: 40px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  -moz-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.08);
  padding: 0 50px 0 20px;
}
.search-icon {
  position: absolute;
  right: 15px;
  color: rgb(116, 116, 116);
  line-height: 40px;
  cursor: pointer;
}
.search-icon i {
  font-size: 23px;
}
</style>
<style>
.stock-table-row {
  height: 60px;
  font-size: 18px;
}
.stock-table-header {
  height: 70px;
  font-size: 20px;
  font-weight: bold;
  color: black;
}
</style>



