<template>
  <div class="order-check-bg">
    <div class="order-check-window">
      <div class="window-top">
        <span>{{$t('m').commodityDetail}}</span>
        <i class="iconfont icon-X" @click="$emit('close')"></i>
      </div>
      <div class="window-content">
        <span class="content-header">
          <img src="../assets/img/apple.png" alt />
        </span>
        <div class="content-text">
          <span>
            <span>{{$t('m').commodityPrice}}:</span>
            <span>{{ data.money }}</span>
          </span>
          <span>
            <span>{{$t('m').deliveryTime}}:</span>
            <span>{{ data.orderTime }}</span>
          </span>
          <span>
            <span>{{$t('m').deviation}}:</span>
            <span>{{ data.offset }}</span>
          </span>
          <span class="tip">{{$t('m').formula}}</span>

          <span>
            <span>{{$t('m').charge}}:</span>
            <span>{{ data.reduce }}</span>
          </span>
          <span>
            <span>{{$t('m').balance}}:</span>
            <span>{{ data.yfk }}</span>
          </span>

          <span>
            <span>{{$t('m').total}}:</span>
            <span>{{ data.yfk }}</span>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import moment from 'moment'
export default {
  name: 'OrderCheck',
  data() {
    return {
      data: {}
    }
  },
  props: ['orderNum'],
  created() {
    this.axios.get(this.$store.state.urls.getOrderByOrderNum + this.orderNum)
      .then(response => {
        let temp = []
        let d = response.data[0]
        d.temperatureVariation.forEach(function(element) {
          temp.push(element.temperature)
        })

        let sortArr = temp.sort(sortNumber)
        let low = d.commodity.lowTempture - sortArr[0]
        if (low < 0 || isNaN(low)) {
          low = 0
        }
        let hight = sortArr[sortArr.length - 1] - d.commodity.highTempture
        if (hight < 0 || isNaN(hight)) {
          hight = 0
        }
        let reduce = low * 0.1 * d.quantity + hight * 0.2 * d.quantity
        let offset = low + hight

        this.data = {
          orderNum: d.id,
          orderTime: moment(new Date(d.orderTime).getTime()).format('YYYY-MM-DD HH:mm:ss'),
          name: d.commodity.name,
          money: (d.quantity * d.commodity.price).toFixed(2) + this.$t('m').yuan,
          offset: offset + '℃',
          reduce: reduce.toFixed(2),
          yfk: (d.quantity * d.commodity.price - reduce).toFixed(2) + this.$t('m').yuan
        }
      })
      .catch(error => {
        this.$message({
          message: this.$t('m').orderErrorMsg,
          type: 'error'
        })
        console.log(error)
      })
    function sortNumber(a, b) {
      return a - b
    }
  }
}
</script>
<style scoped>
@import url("../assets/icon/iconfont.css");
.order-check-bg {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  overflow: hidden;
  text-align: center;
  z-index: 9999;
}
.order-check-bg::after {
  height: 100%;
  width: 0;
  display: inline-block;
  vertical-align: middle;
  content: "";
}
.order-check-window {
  display: inline-block;
  height: 355px;
  vertical-align: middle;
  width: 805px;
  background-color: white;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
}
.window-top {
  height: 40px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #999999;
  border-bottom: 1px solid #44b3f9;
  box-sizing: border-box;
}
.window-content {
  height: calc(100% - 40px);
  padding: 10px 0 10px 20px;
  display: flex;
  box-sizing: border-box;
}
.window-content img {
  height: 60%;
  width: 100%;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.5);
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.5);
}
.window-top i {
  font-size: 16px;
  cursor: pointer;
}
.content-header {
  height: 100%;
  width: 205px;
  padding: 20px 20px 20px 20px;
  box-sizing: border-box;
}
.content-text {
  flex-grow: 1;
  height: 100%;
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.content-text span {
  width: 85%;
  text-align: left;
  line-height: 35px;
}
.content-text span span:first-child {
  margin-right: 15px;
}
.tip {
  border: 1px solid #b9b9b9;
  font-size: 12px;
  line-height: 20px !important;
  padding-left: 10px;
  box-sizing: border-box;
  display: inline-block;
  white-space: normal;
}
</style>



